// Module
___CSS_LOADER_EXPORT___.push([module.id, ".fade:not(.show){opacity:1}", "",{"version":3,"sources":["webpack://./resources/js/components/bus-seat-classes/EditSeatClass.vue"],"names":[],"mappings":"AAmKA,iBACA,SACA","sourcesContent":["<template>\r\n  <div class=\"modal\" id=\"edit-seat-class\">\r\n    <div class=\"modal-dialog\">\r\n      <div class=\"modal-content\">\r\n        <!-- Modal Header -->\r\n        <div class=\"modal-header\">\r\n          <h4 class=\"modal-title\">Edit Seat Class</h4>\r\n          <button type=\"button\" class=\"close\" data-dismiss=\"modal\">\r\n            &times;\r\n          </button>\r\n        </div>\r\n\r\n        <!-- Modal body -->\r\n        <div class=\"modal-body\">\r\n          <validation-observer ref=\"editSeatClass\">\r\n            <form @submit.prevent=\"editSeatClass\" ref=\"form\">\r\n              <div class=\"form-row\">\r\n                <div class=\"col-md-12\">\r\n                  <validation-provider\r\n                    name=\"Amenity Name\"\r\n                    :rules=\"{ required: true, min: 2, max: 20 }\"\r\n                    v-slot=\"validationContext\"\r\n                  >\r\n                    <div class=\"form-group\">\r\n                      <label for=\"name\">Name</label>\r\n                      <input\r\n                        type=\"text\"\r\n                        placeholder=\"Enter class name\"\r\n                        aria-describedby=\"Name-feedback\"\r\n                        v-model.trim=\"seatClass.name\"\r\n                        :class=\"`form-control ${getValidationState(\r\n                          validationContext\r\n                        )}`\"\r\n                      />\r\n                      <div id=\"Name-feed back\" class=\"invalid-feedback w-100\">\r\n                        {{ validationContext.errors[0] }}\r\n                      </div>\r\n                    </div>\r\n                  </validation-provider>\r\n\r\n                  <validation-provider\r\n                    name=\"Color\"\r\n                    :rules=\"{ required: true }\"\r\n                    v-slot=\"validationContext\"\r\n                  >\r\n                    <div class=\"form-group\">\r\n                      <label for=\"icon\">Color</label>\r\n\r\n                      <input\r\n                        type=\"color\"\r\n                        v-model.trim=\"seatClass.color\"\r\n                        :class=\"`form-control ${getValidationState(\r\n                          validationContext\r\n                        )}`\"\r\n                      />\r\n\r\n                      <div id=\"icon-feed back\" class=\"invalid-feedback w-100\">\r\n                        {{ validationContext.errors[0] }}\r\n                      </div>\r\n                    </div>\r\n                  </validation-provider>\r\n\r\n                  <div class=\"form-group\">\r\n                    <div class=\"form-check\">\r\n                      <label class=\"form-check-label\">\r\n                        <input\r\n                          class=\"form-check-input\"\r\n                          type=\"checkbox\"\r\n                          id=\"status\"\r\n                          v-model=\"seatClass.status\"\r\n                        />\r\n                        Active\r\n                      </label>\r\n                    </div>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n\r\n              <div class=\"col-12 my-3 text-right\">\r\n                <button class=\"btn btn-primary\" type=\"submit\">Submit</button>\r\n              </div>\r\n            </form>\r\n          </validation-observer>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport iconpicker from \"fontawesome-iconpicker\";\r\nimport \"fontawesome-iconpicker/dist/css/fontawesome-iconpicker.min.css\";\r\n\r\nexport default {\r\n  emits: [\"update\"],\r\n  props: {\r\n    seatClass: {\r\n      type: Object,\r\n      required: true,\r\n    },\r\n  },\r\n  data() {\r\n    return {};\r\n  },\r\n  methods: {\r\n    editSeatClass() {\r\n      this.$refs.editSeatClass.validate().then((valid) => {\r\n        if (valid) {\r\n          //submit data\r\n          this.$store.dispatch(\"startLoading\");\r\n          axios\r\n            .post(`/admin/bus-seat-classes/${this.seatClass.id}`, {\r\n              name: this.seatClass.name,\r\n              color: this.seatClass.color,\r\n              status: +this.seatClass.status,\r\n              _method: \"PUT\",\r\n            })\r\n            .then((res) => {\r\n              this.closeModal();\r\n\r\n              this.$nextTick(() => {\r\n                this.$emit(\"update\");\r\n                this.showSuccessToast('Bus seat class edited successfully!')\r\n              });\r\n            })\r\n            .catch((res) => {\r\n              console.log(res);\r\n              this.$store.dispatch(\"stopLoading\");\r\n              this.showErrorToast('There was a problem editing the seat class!')\r\n            });\r\n        }\r\n      });\r\n    },\r\n    //---- Validation State Form\r\n    getValidationState({ dirty, validated, valid = null }) {\r\n      if (dirty || validated) {\r\n        return valid ? \"is-valid\" : \"is-invalid\";\r\n      }\r\n      return \"\";\r\n    },\r\n    closeModal() {\r\n      $(\"#edit-seat-class\").modal(\"hide\");\r\n    },\r\n  },\r\n  created() {\r\n    const self = this;\r\n    $(function () {\r\n      $(\".iconpicker-container\").iconpicker({\r\n        input: \".iconpicker-input\", // children input selector\r\n        inputSearch: false, // use the input as a search box too?\r\n        container: false, //  Appends the popover to a specific element. If not set, the selected element or element parent is used\r\n        component: \".input-group-prepend,.iconpicker-element\",\r\n        hideOnSelect: true,\r\n      });\r\n\r\n      $(\".iconpicker-container\").on(\"iconpickerSetValue\", function (event) {\r\n        self.amenity.icon = event.iconpickerValue;\r\n      });\r\n    });\r\n  },\r\n};\r\n</script>\r\n<style>\r\n.fade:not(.show) {\r\n  opacity: 1;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
